	/* Button used to open the contact form - fixed at the bottom of the page */
	.open-button {
	  color: white;
	  padding: 16px 20px;
	  border: none;
	  cursor: pointer;
	  opacity: 0.8;
	  position: fixed;
	  top: 2px;
	  right: 25px;
	  width: 60px;
	}

	.open-button-logged {
	  font-size:14px;
	  text-align:right;
	  color: white;
	  padding: 16px 20px;
	  border: none;
	  cursor: pointer;
	  opacity: 0.8;
	  position: fixed;
	  top: 2px;
	  right: 60px;
	  width: 100px;
	}

	/* The popup form - hidden by default */
	.form-popup {
	  width:100%;
	  display:none;
	  position: fixed;
	  bottom:0;
	  right:0;
	  border:3px solid #f1f1f1;
	  z-index:9;
	  opacity: 1;
	}

	/* Add styles to the form container */
	.form-container {
	  max-width:100%;
	  padding:10px;
	  padding-top: calc(50vh - 170px);
	  height:100vh;
	  background-color:#fff;
	}

	/* Full-width input fields */
	.form-container input[type=text], .form-container input[type=password] {
	  width: 100%;
	  padding: 15px;
	  margin: 5px 0 22px 0;
	  border: none;
	  border-radius: 10px;
	  background: #f1f1f1;
	}

	/* When the inputs get focus, do something */
	.form-container input[type=text]:focus, .form-container input[type=password]:focus {
	  background-color: #ddd;
	  outline: none;
	  border-radius: 10px;
	}

	/* Set a style for the submit/login button*/
	.form-container {
		label {
			margin: auto;
			text-align: center;
			display: inherit;
			color: #505050;
			font-size: 14px;
			margin-bottom: 15px;
		}
		.btn {
			background-color: #4CAF50;
			color: white;
			padding: 16px 20px;
			border: none;
			cursor: pointer;
			font-weight: 700;
			width: 100%;
			border-radius: 10px;
			margin-bottom:10px;
			opacity: 0.8;
		}
	}

	/* Add a red background color to the cancel button */
	.form-container .cancel {
		background-color: transparent;
		color: #3a3a3a;
		font-weight: 800;
		border: 3px solid #9e9e9e;
	}

	/* Add some hover effects to buttons */
	.form-container .btn:hover, .open-button:hover {
	  opacity: 1;
	}

